import { AbstractLink, HyperLinkClickHandler } from '@engine/link';
import { AbstractCellHandler } from './handler.abstract';

export class HyperLinkCellClickHandler extends AbstractCellHandler {
    public eventNames = ['CELL_CLICK'];

    public merger = '';

    public name = 'CELL_HYPERLINK';

    private handler = new HyperLinkClickHandler(this.externalContext.attach);

    public action({ e, hyperLinks }: Parameters<AbstractCellHandler['action']>[0] & { hyperLinks: AbstractLink[] }) {
        this.handler.handle(e!, hyperLinks);
    }
}
